/* eslint-disable */
// CSS
import "../sass/aframe_model_viewer.sass";
// JS
require('aframe-orbit-controls');
import { RGBELoader } from "three/examples/jsm/loaders/RGBELoader";
import { EffectComposer } from "three/examples/jsm/postprocessing/EffectComposer";
import { RenderPass } from "three/examples/jsm/postprocessing/RenderPass";
import { UnrealBloomPass } from "three/examples/jsm/postprocessing/UnrealBloomPass";
import { OutputPass } from "three/examples/jsm/postprocessing/OutputPass";
import { gsap } from "gsap";

//網頁資料下載完成
document.addEventListener('DOMContentLoaded', function() {
    document.querySelector('a-scene').setAttribute('load-complete', '');
});

AFRAME.registerComponent('load-complete', {
    init: function() {
        const loading = document.querySelector('div.loading');
        const sceneEl = document.querySelector('#scene');// SCENE
        let skyEl = document.querySelector('a-sky');
        const cameraEl = document.querySelector('#basic_camera');// CAMERA
        let lightEl = document.querySelector('#basic_light');
        let bgLoading = document.querySelector('div.changeBgLoading');
        let isLoading = false;
        
        gsap.to(loading, { opacity: 0, ease: "power3.inOut", overwrite: true, duration: 1, onComplete: setHide, });
        function setHide(){
            loading.style.display = "none";
        }
        

        function changeBackground(number){
            new RGBELoader().load("images/bg" + number + ".hdr", function(texture){
                texture.mapping = THREE.EquirectangularReflectionMapping;
                texture.image.data = texture.image.data.map(value => value * 0.9);
                sceneEl.object3D.environment = texture;
                skyEl.setAttribute('src', "#sky" + (bgNumber+1));
                if(bgNumber+1 == 1){
                    lightEl.setAttribute('position', '-30 30 -30');
                }else if(bgNumber+1 == 2){
                    lightEl.setAttribute('position', '15 30 2');
                }else if(bgNumber+1 == 3){
                    lightEl.setAttribute('position', '-15 30 2');
                }else if(bgNumber+1 == 4){
                    lightEl.setAttribute('position', '30 30 0');
                }else if(bgNumber+1 == 5){
                    lightEl.setAttribute('position', '25 60 0');
                }else if(bgNumber+1 == 6){
                    lightEl.setAttribute('position', '-10 60 -20');
                }else if(bgNumber+1 == 7){
                    lightEl.setAttribute('position', '6 60 -20');
                }
                bgBtn.style.opacity = "1";
                isLoading = false;
                gsap.to(bgLoading, { opacity: 0, ease: "power3.inOut", overwrite: true, duration: 1, onComplete: setBgLoadingHide, });
                function setBgLoadingHide(){
                    bgLoading.style.display = "none";
                }
            });
        }

        // // Bloom setting
        // const renderScene = new RenderPass( sceneEl.object3D, sceneEl.camera );
        // const bloomPass = new UnrealBloomPass( new THREE.Vector2( window.innerWidth, window.innerHeight ), 1.5, 0.4, 0.85 );
		// bloomPass.threshold = 1;
		// bloomPass.strength = 0.33;
		// bloomPass.radius = 0;
        // const outputPass = new OutputPass();
        // const renderer = sceneEl.renderer;
        // const render = renderer.render;

        // // Composer setting
        // const composer = new EffectComposer( renderer );
        // composer.addPass( renderScene );
        // composer.addPass( bloomPass );
        // composer.addPass( outputPass );
        // let isDigest = false;
        // renderer.render = function () {
        //     if (isDigest) {
        //         render.apply(renderer, arguments);
        //     } else {
        //         isDigest = true;
        //         composer.render();
        //         isDigest = false;
        //     }
        // };
        // let box = document.querySelector('#myBox');
        let bgBtn = document.querySelector('p.bgBtn');
        let qmimi = document.querySelector('#qmimi');
        let qmimiTouch = false;
        qmimi.addEventListener('click', function () {
            if(qmimi.getAttribute('animation-mixer').clip == 'Stand'){
                qmimi.setAttribute('animation-mixer','clip','Walk');
            }else if(qmimi.getAttribute('animation-mixer').clip == 'Walk'){
                qmimi.setAttribute('animation-mixer','clip','Run');
            }else if(qmimi.getAttribute('animation-mixer').clip == 'Run'){
                qmimi.setAttribute('animation-mixer','clip','Stand');
            }
        });
        let bgNumber = 0;
        // 添加点击事件监听器
        changeBackground(1);
        bgBtn.addEventListener('click', function () {
            if(!isLoading){
                bgLoading.style.opacity = "1";
                bgLoading.style.display = "block";
                isLoading = true;
                bgBtn.style.opacity = "0.3";
                if(bgNumber + 1 == 7){
                    bgNumber = 0;
                }else{
                    bgNumber++;
                }
                changeBackground(bgNumber+1);
            }
        });
        let mouseDownX = 0;
        let mouseDownY = 0;
        let qy;
        qmimi.addEventListener('mousedown',function(event){
            cameraEl.setAttribute('look-controls-enabled', 'false');
            qmimiTouch = true;
        });
        document.addEventListener('touchstart',function(event){
            mouseDownX = event.touches[0].clientX;
            mouseDownY = event.touches[0].clientY;
            qy = qmimi.getAttribute('rotation').y;
        });
        document.addEventListener('touchmove',function(event){
            if(qmimiTouch){
                let _ry = qy + (event.touches[0].clientX - mouseDownX)/2;
                qmimi.setAttribute('rotation','0 ' + _ry + ' 0');
            }
        });
        document.addEventListener('touchend',function(event){
            cameraEl.setAttribute('look-controls-enabled', 'true');
            qmimiTouch = false;
            mouseDownX = 0;
            mouseDownY = 0;
        });
        document.addEventListener('mousedown',function(event){
            mouseDownX = event.clientX;
            mouseDownY = event.clientY;
            qy = qmimi.getAttribute('rotation').y;
        });
        document.addEventListener('mousemove',function(event){
            if(qmimiTouch){
                let _ry = qy + (event.clientX - mouseDownX)/2;
                qmimi.setAttribute('rotation','0 ' + _ry + ' 0');
            }
        });
        document.addEventListener('mouseup',function(event){
            cameraEl.setAttribute('look-controls-enabled', 'true');
            qmimiTouch = false;
            mouseDownX = 0;
            mouseDownY = 0;
        });
    
        window.addEventListener( 'resize', onWindowResize );
        function onWindowResize() {
            const _width = window.innerWidth;
			const _height = window.innerHeight;
            // composer.setSize( parseInt(_width), parseInt(_height) );
            sceneEl.camera.aspect = parseInt(_width)/parseInt(_height);
            sceneEl.camera.updateProjectionMatrix();
		}
        onWindowResize();
    }
});